@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Atkinson+Hyperlegible&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400&display=swap);
* {
  margin: 0;
  padding: 0;
  outline: none;
  border: none;
}

body {
  box-sizing: border-box;
}

.app {
  height: 100vh;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}

@media only screen and (max-width: 700px) {
  .app {
    height: auto;
    box-sizing: border-box;
  }
}

.joinWaitlist__container {
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
  background-color: #333333;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 75px 100px;
  display: flex;
  flex-direction: column;
}

.joinWaitlist__topContainer {
  display: flex;
  border-radius: 20px;
  background-color: #f5f5f5;
  flex-grow: 1;
  height: 100%;
}

.joinWaitlist__topContainerLeft {
  padding: 40px 20px 0px;
  width: 65%;
  position: relative;
}

.joinWaitlist__leftArrow {
  position: absolute;
  top: 20px;
  left: 20px;
  background-color: transparent;
}

.joinWaitlist__topContainerRight {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #4446bf;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  padding: 120px 70px;
  width: 35%;
}

.joinWaitlist__logo {
  width: 70%;
  min-width: 200px;
}

.joinWaitlist__footer {
  padding: 10px;
  position: absolute;
  bottom: 0;
}

.joinWaitlist__footer > * {
  margin: 10px;
}

@media only screen and (max-width: 1100px) {
  .joinWaitlist__container {
    background-color: #f5f5f5;
    padding: 0;
  }

  .joinWaitlist__topContainerLeft {
    width: 100vw;
    /* height: 100vh; */
    position: static;
    padding: 50px 30px 0;
  }

  .joinWaitlist__footer {
    position: static;
  }

  .joinWaitlist__topContainerRight {
    display: none;
  }
}

.joinWaitlist__textContent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Source Sans Pro", sans-serif;
  color: rgb(51, 51, 51);
  font-size: 1.2em;
}

.joinWaitlist__textContentHeader {
  text-align: center;
  font-size: 2.3rem;
  font-weight: 600;
}

.joinWaitlist__textContent p {
  min-width: 400px;
  width: 91%;
  text-align: center;
  color: #70737c;
  margin: 5px;
}

.joinWaitlist__form {
  padding: 10px 50px;
  display: flex;
  flex-direction: column;
}

.joinWaitlist__input {
  margin: 15px 0px;
  padding: 10px;
  font-size: 1.2rem;
  border: 1px solid rgb(97, 96, 96);
  font-family: "Atkinson Hyperlegible", sans-serif;
  font-weight: 600;
  color: #333333;
  border-radius: 10px;
}

.joinWaitlist__submitBtn {
  padding: 10px;
  border-radius: 3px;
  border: none;
  cursor: pointer;
  outline: none;
  margin: auto;
  margin-top: 18px;
  height: 40px;
  text-align: center;
  width: 130px;
  border-radius: 40px;
  background: #fff;
  border: 2px solid #4446bf;
  color: #4446bf;
  letter-spacing: 1px;
  text-shadow: 0;
  font-size: 1.1rem;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.25s ease;
  font-family: "Source Sans Pro", sans-serif;
  padding: 10px 20px;
  width: 170px;
  padding-bottom: 35px;
  margin-bottom: 10px;
}

.joinWaitlist__submitBtn:hover {
  color: white;
  background: #4446bf;
}
.joinWaitlist__submitBtn:active {
  letter-spacing: 2px;
}
.onclic {
  width: 40px;
  border-color: #bbb;
  /* border-width: 3px; */
  border-left-color: #4446bf;
  -webkit-animation: rotating 2s 0.25s linear infinite;
          animation: rotating 2s 0.25s linear infinite;
}
.onclic:after {
  content: "";
}
.onclic:hover {
  color: #4446bf;
  background: white;
}
.validate {
  font-size: 13px;
  color: white;
  background: #4446bf;
}
.validate:after {
  font-family: "FontAwesome";
}
@-webkit-keyframes rotating {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes rotating {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.joinWaitlist__error {
  color: red;
  font-size: 0.75rem;
  font-family: "Source Sans Pro", sans-serif;
  position: relative;
  bottom: 10px;
  margin-bottom: -10px;
}

@media only screen and (max-width: 1300px) {
  .joinWaitlist__submitBtn {
    margin-top: 10px;
  }
}

@media only screen and (max-width: 1100px) {
  body {
    background-color: #f5f5f5;
  }

  .joinWaitlist__container {
    padding: 0;
  }
  .joinWaitlist__textContent {
    padding: 0 10px;
  }

  .joinWaitlist__textContentHeader {
    font-size: 2.5rem;
  }

  .joinWaitlist__form {
    padding: 20px 50px;
    align-items: center;
  }
  .joinWaitlist__input {
    margin: 20px 0;
    width: 90%;
    max-width: 500px;
    border: 1px solid rgb(97, 96, 96);
  }

  .joinWaitlist__submitBtn {
    margin-top: 20px;
  }

  .joinWaitlist__textContent p {
    width: 80%;
  }
}

@media only screen and (max-width: 500px) {
  .joinWaitlist__form {
    padding: 30px 0;
  }
  .joinWaitlist__input {
    width: 100%;
  }
}

@media only screen and (max-width: 400px) {
  .joinWaitlist__textContentHeader {
    font-size: 1.9rem;
    width: 100%;
  }

  .joinWaitlist__textContent p {
    min-width: 100px;
    width: 100%;
    margin: 15px 0;
    font-size: 1.2rem;
  }

  .joinWaitlist__input {
    margin: 20px 0;
    font-size: 1.3rem;
    padding: 15px 20px;
    border: none;
    border: 1px solid rgb(97, 96, 96);
  }

  .joinWaitlist__submitBtn {
    font-size: 1.5rem;
    margin-top: 20px;
    width: 200px;
    padding: 10px;
    padding-bottom: 40px;
  }
}

.footer__socialLinks {
  padding: 10px;
  display: flex;
  justify-content: center;
}

.footer__socialLinks > * {
  margin: 0px 10px;
}

.signUpSuccess__container {
  padding: 20px;
  font-family: "Source Sans Pro", sans-serif;
  color: rgb(51, 51, 51);
  text-align: center;
  height: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.signUpSuccess__subHeader {
  color: #70737c;
}

.signUpSuccessHeader {
  position: absolute;
  bottom: 10px;
  display: none;
}

@media only screen and (max-width: 1100px) {
  .signUpSuccess__container {
    height: 60%;
  }

  .signUpSuccessHeader {
    display: unset;
  }
}

.signUpFail__container {
  text-align: center;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.signUpFail__header {
  font-family: "Source Sans Pro", sans-serif;
  color: rgb(51, 51, 51);
  text-align: center;
}

.signUpFail__btn {
  margin-top: 20px;
}

@media only screen and (max-width: 1100px) {
  .signUpFail__container {
    height: 60%;
  }
}

.navbar__container {
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1), 0 4px 10px 0 rgba(0, 0, 0, 0.15);
}

.navbar__getEarlyAccessButton {
  color: #4446bf;
  border-color: #4446bf;
}

.navbar__getEarlyAccessButton:hover {
  color: #fff;
  background-color: #4446bf;
  border-color: #4446bf;
}

.navbar__logo {
  width: 70px;
  margin: 0 10px;
}

.middle__container {
  padding: 20px 20px;
  display: flex;
  box-sizing: border-box;
  overflow: hidden;
  flex-grow: 1;
}

.middle__leftContainer {
  width: 50%;
  height: 80%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.middle__leftContainer > * {
  margin: 13px 0;
}

.middle__rightContainer {
  flex-grow: 1;
  background-color: teal;
}

.middle__heading {
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 600;
  width: 90%;
}

.middle__subHeading {
  font-family: "Source Sans Pro", sans-serif;
  color: rgb(82, 78, 78);
  font-size: 1.5rem;
  width: 90%;
}

.middle__getEarlyAccessButton {
  color: #4446bf;
  border-color: #4446bf;
  font-size: 1.2rem;
  font-family: "Source Sans Pro", sans-serif;
}

.middle__getEarlyAccessButton:hover {
  color: #fff;
  background-color: #4446bf;
  border-color: #4446bf;
}

@media only screen and (max-width: 700px) {
  .middle__container {
    flex-direction: column;
    align-items: stretch;
    padding: 10px 5px;
  }

  .middle__leftContainer {
    width: 100%;
    align-items: center;
    text-align: center;
  }

  .middle__leftContainer > * {
    margin: 15px 0;
  }

  .middle__rightContainer {
    width: 100%;
    height: 300px;
  }
}

